import React from 'react';
import { Model } from '../model';
import Atlas, { State as AtlasMode } from './atlas/Atlas';
import { Toolbar, Mode as ToolbarMode } from './Toolbar';
import { connect } from 'react-redux';
import { Dispatch } from 'redux';
import UndoButtons from './generic/UndoButtons';
import { Msg } from '../actions';
import { Undoable } from '../utils/history';


/*************************** STATE ****************************/

export interface AppState {
    readonly model : Undoable<Model>;
}

export interface UIState {
    readonly atlas : AtlasMode,
    readonly toolbar : ToolbarMode
}

export interface State extends AppState {
    readonly ui : UIState
}


/*************************** APPLICATION ****************************/

interface Props {
    state : State,
    dispatch : Dispatch<Msg>
}

const _App = ({ state, dispatch } : Props) => (
    <div>

        <Atlas
            style = {{position: "absolute", height: "100%", width: "100%"}} 
            />

        <div style={{position: "fixed", top: "2rem", left: "1.5rem", maxWidth: "400px"}}>
            <Toolbar />
        </div>

        <UndoButtons
            style   = {{position: "fixed", top: "2rem", right: "1.5rem"}}
            undo    = {() => dispatch({ type : '@history/undo' })}
            redo    = {() => dispatch({ type : '@history/redo' })}
            canUndo = {state.model.past.length > 0}
            canRedo = {state.model.future.length > 0}
            />

    </div>
);

const App = connect(
    (state : State) => { return { state } }, 
    (dispatch : Dispatch<Msg>) => { return { dispatch } }
)(_App);

export default App;