import React from 'react';
import { faPlus, faExclamationTriangle, faMinusCircle } from '@fortawesome/free-solid-svg-icons'
import { Color, Handler, Pathway } from '../../model';
import SelectColor from './SelectColor';
import Icon from '../generic/Icon';
import ToolbarHeader from './ToolbarHeader';

interface Props {
    oldState? : Pathway,
    onSubmit : Handler<Pathway>,
    onDelete? : Handler<any>,
    goBack : Handler<any>,
    title : string,
    submitButtonText : string,
}

interface State {
    name : string,
    color? : Color,
    noNameError : boolean,
    noColorError : boolean,
}

export default class EditPathway extends React.Component<Props, State> {
    constructor(props : Props) {
        super(props);

        let existingPathway = this.props.oldState;
        this.state = { 
            name: existingPathway ? existingPathway.name : "", 
            color: existingPathway ? existingPathway.color : undefined, 
            noNameError: false, noColorError: false 
        };

        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.setColor = this.setColor.bind(this);
    }
    handleChange(event : React.ChangeEvent<HTMLInputElement>) {
        const name = event.target.value;

        this.setState({ 
            name: name, 
            noNameError: this.state.noNameError && name.trim() === '',
        });
    }
    setColor(color : Color) {
        this.setState({ color: color, noColorError: false });
    }
    handleSubmit(event : React.FormEvent) {
        event.preventDefault();

        let error = false;
        // Make sure the name is nonempty and a color is selected
        if (this.state.color === undefined) {
            this.setState({ noColorError: true });
            error = true;
        }
        if (this.state.name.trim() === '') {
            this.setState({ noNameError: true });
            error = true;
        }
        if (!error) {
            // Notice that if `color` were undefined then `error` would be true.
            // Therefore we can assert it isn't undefined below.
            this.props.onSubmit({
                name: this.state.name.trim(),
                color: this.state.color as Color
            });
        }
    }
    render() {
        return (
        <ToolbarHeader title={this.props.title} goBack={this.props.goBack}>

            <form className="panel-block" onSubmit={this.handleSubmit} style={{display: "block"}}>
            <div className="field">
                <label className="label">Name</label>
                <div className="control">
                    <input className="input" placeholder="Pathway name" type="text" value={this.state.name} onChange={this.handleChange} />
                </div>
                {this.state.noNameError && 
                    <p className="help is-danger">
                        <Icon icon={faExclamationTriangle} />
                        Please enter a name for the pathway.
                    </p> 
                }
            </div>

            <div className="field">
                <label className="label">Color</label>
                <SelectColor selected={this.state.color} onClick={this.setColor} />
                {this.state.noColorError && 
                    <p className="help is-danger">
                        <Icon icon={faExclamationTriangle} />
                        Please select a color for this pathway.
                    </p> 
                }
            </div>

            <div className="buttons">
            <button className="button is-primary is-fullwidth">
                <Icon icon={faPlus} />
                <span>{this.props.submitButtonText}</span>
            </button>

            { this.props.onDelete &&
                <button className="button is-hover is-danger is-outlined is-fullwidth" 
                        type="button" onClick={this.props.onDelete!}>
                    <Icon icon={faMinusCircle} />
                    <span>Delete Pathway</span>
                </button>
            }
            </div>
        </form>
        </ToolbarHeader>);
    }
}