
/**
 * This is a wrapper class for persisting application state to the browser.
 * Instead of writing all the time, it schedules updates to take place. 
 */
export default function<T>(DB_NAME : string, PERSIST_DATA_TIMEOUT : number) 
    : { put : (data : T) => void, get : () => T | undefined }
{

    let isDirty : Boolean = false;
    let cache : T | null  = null;
    
    function persistData() {
        localStorage.setItem(DB_NAME, JSON.stringify(cache));
        isDirty = false;
    }

    function put(data : T) {
        cache = data;
        if (!isDirty) {
            setTimeout(persistData, PERSIST_DATA_TIMEOUT);
        }
        isDirty = true;
    }

    function get() : T | undefined {
        const savedState = localStorage.getItem(DB_NAME);

        if (savedState === null) {
            return undefined;
        }
        else {
            return JSON.parse(savedState) as T;
        }
    }

    return { put, get };
}
