import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { IconProp } from '@fortawesome/fontawesome-svg-core';
import { Color } from '../../model';

interface Props {
    icon : IconProp,
    color? : Color,
    container? : 'small' | 'medium' | 'large',
    iconSize? : 'lg' | '2x' | '3x'
}

export default function Icon(props : Props) {
    const color    = props.color ? 'has-text-' + props.color : '';
    const spanSize = props.container ? 'is-' + props.container : '';
    const spanClass = ['icon', spanSize, color].join(' ');

    return (<span className={spanClass}>
                <FontAwesomeIcon icon={props.icon} size={props.iconSize} />
            </span>);
}
                