import isEqual from 'lodash/isEqual';

/**
 * Wraps a plain JSON object with undo/redo functionality
 */
export type Undoable<T> = {
    present : T;
    past    : T[];
    future  : T[];
}

export type UndoMsg
    = { type : '@history/undo' }
    | { type : '@history/redo' }

export type Config = {
    limit : number
}


/**
 * Higher-order reducer for making the state undoable.
 */
export function undoable<State, Msg extends { type : string }>(
    config : Config, 
    reducer : (state : State | undefined, msg : Msg) => State
) 
: (state : Undoable<State> | undefined, msg : UndoMsg | Msg) => Undoable<State>
{

    /**
     * If the undo history of the argument is nonempty, returns a *new* object
     * whose current state is the top of the undo stack.
     */
    function undo<T>(data : Undoable<T>) : Undoable<T> | undefined {
        if (data.past.length > 0) {
            // Make a copy of 'redo' with the current model pushed onto it
            const redo = data.future.slice();
            redo.push(data.present);

            // Make a copy of 'redo' with the new model popped from it
            const undo = data.past.slice();
            const model = undo.pop() as T;

            return { present: model, past: undo, future: redo };
        }
    }

    /**
     * If the redo history of the argument is nonempty, returns a *new* object
     * whose current state is the top of the redo stack.
     * Otherwise, returns the same argument unchanged.
     */
    function redo<T>(data : Undoable<T>) : Undoable<T> | undefined {
        if (data.future.length > 0) {
            // Make a copy of 'undo' with the current model pushed onto it
            const undo = data.past.slice();
            undo.push(data.present);

            // Make a copy of 'redo' with the new model popped from it
            const redo = data.future.slice();
            const model = redo.pop() as T;

            return { present: model, past: undo, future: redo };
        }
    }

    /**
     * Transition the history to a new state; `present` gets added to the latest
     * part of the `past` stack and the `future` is forgotten. Does not make a
     * change if `nextState` is the same as `history.present`. States beyond
     * `config.limit` are forgotten.
     */
    function setState<T>(_history : Undoable<T | undefined>, nextState : T) : Undoable<T> {
        if (_history.present === undefined) {
            return { past : [], future : [], present : nextState };
        }
        // Now assert that history must have been initialized
        const history = _history as Undoable<T>;

        // Don't add to history if the two objects are structurally identical
        if (isEqual(nextState, history.present)) {
            return history; 
        }
        const overflow = history.past.length >= config.limit ? 1 : 0;
        const past = history.past.concat([history.present]).slice(overflow)
        const future : T[] = [];
        const present = nextState;
        return { past, present, future };
    }


    const initialState : Undoable<State | undefined> = {
        past : [],
        future : [],
        present : undefined
    };

    return (
        state : Undoable<State | undefined> | undefined = initialState,
        msg : UndoMsg | Msg
    ) => {
        // We expect that undo/redo will not be invoked on an undefined initial state.
        switch(msg.type) {
            case '@history/undo':
                return undo(state as Undoable<State>) || state as Undoable<State>;

            case '@history/redo':
                return redo(state) as Undoable<State> || state as Undoable<State>;

            default:
                // Initializes the undefined initial state
                const nextState = reducer(state.present, msg as Msg);
                return setState(state, nextState);
        }
    };
}