import { State, Mode } from "../components/atlas/Atlas";
import { UIMsg, AtlasMsg } from "../actions";
import { PathwayId, PartialLink } from "../model";

const Normal : Mode = { type : 'Normal' };

const CreateNode : Mode = { type : 'CreateNode' };

const SelectLinkSource = (pathway : PathwayId) : Mode => 
    ({ type : 'SelectLinkSource', pathway });

const SelectLinkTarget = (link : PartialLink) : Mode => 
    ({ type : 'SelectLinkTarget', link });


export function atlas(
    state : State | undefined = { mode : Normal },
    msg : UIMsg | AtlasMsg
) : State {
    switch (msg.type) {
        case 'go home':
            return { ...state, mode : Normal };

        case 'prompt to select a location for new node':
            return { ...state, mode : CreateNode };

        case 'prompt to create node with coords':
            return { ...state, mode : Normal };

        case 'set mouse position':
            return { ...state, cursor : msg.coords };

        case 'prompt to select link source':
            return { ...state, mode : SelectLinkSource(msg.pathway) };

        case 'prompt to select link target':
            return { ...state, mode : SelectLinkTarget(msg.link) };

        default:
            return state;
    }
}