/**
 * This module defines a collection of different Toolbars. Crucially, each
 * Toolbar function should not contain side-effects, since it is an
 * ordinary React component. Side-effects like downloading a file should
 * be triggered by an *event* such as onClick, rather than be triggered
 * in the body of the function itself.
 */

import React from 'react';
import { Dispatch } from 'redux';
import { Model, Pathway, PathwayId, Node, Coords, NodeId } from '../model';
import EditPathway from './toolbars/EditPathway';
import SelectOrCreatePathway from './toolbars/SelectOrCreatePathway';
import { faBoxes, faBezierCurve, faEdit, faPlusCircle, faDownload, faUpload } from '@fortawesome/free-solid-svg-icons';
import { saveAs } from 'file-saver';
import ImportData from './toolbars/ImportData';
import ToolbarHeader from './toolbars/ToolbarHeader';
import { State } from './App';
import { connect } from 'react-redux';
import { Panel } from './generic/Panel';
import { Msg } from '../actions'
import EditNode from './toolbars/EditNode';

 
export type Mode 
    = { type : 'Home' }
    | { type : 'SelectNodeLocation' }
    | { type : 'CreatePathway' }
    | { type : 'SelectPathwayToEdit' }
    | { type : 'EditThisPathway', id : PathwayId }
    | { type : 'ImportDataAction' }
    | { type : 'CreateNode', coords : Coords }
    | { type : 'EditNode', id : NodeId }
    | { type : 'SelectPathwayForLink' }
    | { type : 'CreateLink', pathway : PathwayId }


interface Props {
    mode : Mode,
    model : Model,
    dispatch : Dispatch<Msg>
}

function stateToProps(state : State) {
    return { mode : state.ui.toolbar, model : state.model.present };
}

function dispatchToProps(dispatch : Dispatch<Msg>) {
    return { dispatch };
}


/**** Home toolbar ****/

function _Home({ model, dispatch } : Props) {
    const heading = "Select an Action";
    const panelItems = [
        {
            text : "Create Node",
            icon : faBoxes,
            onClick : () => {
                dispatch({ type : 'prompt to select a location for new node' });
            }
        },
        {
            text : "Create Link",
            icon : faBezierCurve,
            onClick : () => {
                dispatch({ type : 'prompt to select a pathway for new links' })
            }
        },
        {
            text : "Create Pathway",
            icon : faPlusCircle,
            onClick : () => {
                dispatch({ type : 'prompt to create a pathway' });
            }
        },
        {
            text : "Edit Pathway",
            icon : faEdit,
            onClick : () => {
                dispatch({ type : 'prompt to select a pathway for editing' });
            }
        },
        {
            text : "Import Data",
            icon : faUpload,
            onClick : () => {
                dispatch({ type : 'prompt to upload app state' });
            }
        },
        {
            text : "Export Data",
            icon : faDownload,
            onClick : () => downloadJSON(model)
        }
    ];

    return <Panel heading={heading} panelItems={panelItems} />;
}

function downloadJSON<T>(json : T) {
    const blob = new Blob([JSON.stringify(json, null, 2)], {type: "application/json;charset=utf-8"});
    saveAs(blob, "atlas.json");
}

const Home = connect(stateToProps, dispatchToProps)(_Home);


/**** Workflow for the "create node" option ****/

function _SelectNodeLocation({ dispatch } : Props) {
    const goBack = () => {
        dispatch({ type : 'go home' });
    };
    return <ToolbarHeader title="Place a Node" goBack={goBack} />;
}

const SelectNodeLocation = connect(stateToProps, dispatchToProps)(_SelectNodeLocation);


function _CreateNode({ dispatch, coords } : Props & { coords : Coords }) {
    const onSubmit = (node : Node) => {
        dispatch({ type : 'create node', node });
        dispatch({ type : 'go home' });
    };
    const goBack = () => {
        dispatch({ type : 'go home' });
    };

    return <EditNode
        title="Create Node"
        submitButtonText="Create"
        oldState={{ name: "", coords }}
        onSubmit={onSubmit}
        goBack={goBack}
    />
}

const CreateNode = connect(stateToProps, dispatchToProps)(_CreateNode);


function _EditNodeToolbar({ dispatch, model, id } : Props & { id : NodeId }) {
    const onSubmit = (node : Node) => {
        dispatch({ type : 'update node', node, id });
        dispatch({ type : 'go home' });
    };
    const goBack = () => {
        dispatch({ type : 'go home' });
    };
    const onDelete = () => {
        dispatch({ type : 'go home' });
        dispatch({ type : 'delete node', id });
    }
    const node = model.nodes[id];

    return <EditNode
        title="Edit Node"
        submitButtonText="Update"
        oldState={node}
        onSubmit={onSubmit}
        goBack={goBack}
        onDelete={onDelete}
    />
}

const EditNodeToolbar = connect(stateToProps, dispatchToProps)(_EditNodeToolbar);

/**** Workflow for the "create pathway" option ****/

function _CreatePathway({ model, dispatch } : Props) {
    const onSubmit = (pathway : Pathway) => {
        dispatch({ type : 'create pathway', pathway });
        dispatch({ type : 'go home' });
    };
    const goBack = () => {
        dispatch({ type : 'go home' });
    };
    return <EditPathway {...model} 
                onSubmit={onSubmit} 
                goBack={goBack}
                title="Create a Pathway"
                submitButtonText="Create" />
}

const CreatePathway = connect(stateToProps, dispatchToProps)(_CreatePathway);


/**** Workflow for the "edit pathway" option ****/

function _SelectPathwayToEdit({ model, dispatch } : Props) {
    const onSelect = (id : PathwayId) => {
        dispatch({ type : 'prompt to edit pathway', id});
    };
    const goBack = () => {
        dispatch({ type : 'go home' });
    };
    return <SelectOrCreatePathway 
                pathways={model.pathways} 
                goBack={goBack}
                onSelect={onSelect} />;
}

const SelectPathwayToEdit = connect(stateToProps, dispatchToProps)(_SelectPathwayToEdit);


function _EditThisPathway({ model, dispatch, id } : Props & { id : PathwayId }) {
    const onSubmit = (pathway : Pathway) => {
        dispatch({ type : 'update pathway', id, pathway });
        dispatch({ type : 'prompt to select a pathway for editing'});
    };
    const onDelete = () => {
        dispatch({ type : 'delete pathway', id });
        dispatch({ type : 'prompt to select a pathway for editing'});
    };
    const goBack = () => {
        dispatch({ type : 'prompt to select a pathway for editing'});
    };

    return <EditPathway 
                oldState={model.pathways[id]}
                onSubmit={onSubmit} 
                onDelete={onDelete}
                goBack={goBack}
                title="Edit Pathway"
                submitButtonText="Confirm changes" />;
}

const EditThisPathway = connect(stateToProps, dispatchToProps)(_EditThisPathway);


/**** Workflow for the "export data" option ****/

function _ImportDataAction({ model, dispatch } : Props) {
    const onUpload = (model : Model) => {
        dispatch({ type : 'set model', model });
        dispatch({ type : 'go home' });
    };
    const goBack = () => {
        dispatch({ type : 'go home' });
    };

    return <ImportData {...model} 
                goBack={goBack} 
                setModel={onUpload} />;
}

const ImportDataAction = connect(stateToProps, dispatchToProps)(_ImportDataAction);


/**** Workflow for the "create link" option ****/

function _SelectPathwayForLink({ model, dispatch } : Props) {
    const onSelect = (id : PathwayId) => {
        dispatch({ type : 'prompt to select link source', pathway : id });
    };
    const goBack = () => {
        dispatch({ type : 'go home' });
    };
    return <SelectOrCreatePathway 
                pathways={model.pathways} 
                goBack={goBack}
                onSelect={onSelect} />;
}
const SelectPathwayForLink = connect(stateToProps, dispatchToProps)(_SelectPathwayForLink);


function _CreateLink({ dispatch, pathway } : Props & { pathway : PathwayId }) {
    const goBack = () => {
        dispatch({ type : 'go home' });
    };
    return <ToolbarHeader title="Create Links" goBack={goBack} />;
}
const CreateLink = connect(stateToProps, dispatchToProps)(_CreateLink);


/***** Dispatch which toolbar to use *****/

function _Toolbar({ mode } : Props) : JSX.Element {

    switch (mode.type) {
        case 'Home': 
            return <Home />;
        case 'SelectNodeLocation':
            return <SelectNodeLocation />;
        case 'CreatePathway': 
            return <CreatePathway />;
        case 'SelectPathwayToEdit':
            return <SelectPathwayToEdit />;
        case 'EditThisPathway': 
            return <EditThisPathway id={mode.id} />;
        case 'ImportDataAction':
            return <ImportDataAction />;
        case 'CreateNode':
            return <CreateNode coords={mode.coords} />;
        case 'EditNode':
            return <EditNodeToolbar id={mode.id} />;
        case 'SelectPathwayForLink':
            return <SelectPathwayForLink />;
        case 'CreateLink':
            return <CreateLink pathway={mode.pathway} />;
    }
}

export const Toolbar = connect(stateToProps, dispatchToProps)(_Toolbar);
