import React from 'react';
import { faPlus, faExclamationTriangle, faMinusCircle } from '@fortawesome/free-solid-svg-icons'
import { Handler, Node } from '../../model';
import Icon from '../generic/Icon';
import ToolbarHeader from './ToolbarHeader';

interface Props {
    oldState : Node,
    onSubmit  : Handler<Node>,
    onDelete? : Handler<any>,
    goBack : Handler<any>,
    title : string,
    submitButtonText : string,
}

interface State {
    name : string,
    noNameError : boolean,
}

export default class EditNode extends React.Component<Props, State> {
    constructor(props : Props) {
        super(props);

        this.state = { 
            name: this.props.oldState.name,
            noNameError: false,
        };

        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }
    // We implement this method in case the props change and we need to edit a
    // different node.
    componentDidUpdate(prevProps : Props) {
        if (prevProps.oldState !== this.props.oldState) {
            this.setState({ name : this.props.oldState.name, noNameError: false });
        }
    }
    handleChange(event : React.ChangeEvent<HTMLInputElement>) {
        const name = event.target.value;

        this.setState({ 
            name: name, 
            noNameError: this.state.noNameError && name.trim() === '',
        });
    }
    handleSubmit(event : React.FormEvent) {
        event.preventDefault();

        let error = false;
        // Make sure the name is nonempty
        if (this.state.name.trim() === '') {
            this.setState({ noNameError: true });
            error = true;
        }
        if (!error) {
            this.props.onSubmit({
                name: this.state.name.trim(),
                coords: this.props.oldState.coords
            });
        }
    }
    render() {
        return (
        <ToolbarHeader title={this.props.title} goBack={this.props.goBack}>

            <form className="panel-block" onSubmit={this.handleSubmit} style={{display: "block"}}>
            <div className="field">
                <label className="label">Name</label>
                <div className="control">
                    <input className="input" placeholder="Node name" type="text" value={this.state.name} onChange={this.handleChange} />
                </div>
                {this.state.noNameError && 
                    <p className="help is-danger">
                        <Icon icon={faExclamationTriangle} />
                        Please enter a name for the node.
                    </p> 
                }
            </div>

            <div className="buttons">
            <button className="button is-primary is-fullwidth">
                <Icon icon={faPlus} />
                <span>{this.props.submitButtonText}</span>
            </button>

            { this.props.onDelete &&
                <button className="button is-hover is-danger is-outlined is-fullwidth" 
                        type="button" onClick={this.props.onDelete!}>
                    <Icon icon={faMinusCircle} />
                    <span>Delete Node</span>
                </button>
            }
            </div>
        </form>
        </ToolbarHeader>);
    }
}