import { Mode } from '../components/Toolbar';
import { UIMsg } from '../actions';

export const initialMode : Mode = { type : 'Home' };

export function toolbar(
    mode : Mode | undefined = initialMode, 
    msg : UIMsg
) : Mode {
    switch (msg.type) {
        case 'go home': 
            return { type : 'Home' };

        case 'prompt to create a pathway': 
            return { type : 'CreatePathway' };

        case 'prompt to select a pathway for editing':
            return { type : 'SelectPathwayToEdit' };

        case 'prompt to edit pathway':
            return { type : 'EditThisPathway', id : msg.id };

        case 'prompt to upload app state':
            return { type : 'ImportDataAction' };

        case 'prompt to select a location for new node':
            return { type : 'SelectNodeLocation' };

        case 'prompt to create node with coords':
            return { type : 'CreateNode', coords : msg.coords };

        case 'prompt to edit node':
            return { type : 'EditNode', id : msg.id };

        case 'prompt to select a pathway for new links':
            return { type : 'SelectPathwayForLink' };

        case 'prompt to select link source':
            return { type : 'CreateLink', pathway : msg.pathway };

        default:
            return mode;
    }
}