import React from 'react';
import ReactDOM from 'react-dom';
import { createStore } from 'redux';
import { Provider } from 'react-redux';
import App, { AppState } from './components/App';
import './style.scss';
import LocalDBFactory from './utils/persistence';
import { localDBName, persistDataTimeout } from './config';
import { rootReducer } from './reducers/rootReducer';


/************ Local storage stuff **************/

// Create a connection to local storage
const LocalDB = LocalDBFactory<AppState>(localDBName, persistDataTimeout);

// Tries to initialize application state.
// Reducers automatically populate the ui state.
const state = LocalDB.get();

// Persists data to local storage
const onUpdate = () => {
    const { model } = store.getState();
    LocalDB.put({ model });
}


/************ Render the application ************/

const store = createStore(rootReducer, state);
store.subscribe(onUpdate);

ReactDOM.render(
    <Provider store={store}>
        <App />,
    </Provider>, 
    document.getElementById("root"));