import React, { RefObject } from 'react';
import * as d3 from 'd3';
import { Msg } from '../../actions';
import { Coords } from '../../model';

interface Props {
    style : React.CSSProperties,
    dispatch : (msg : Msg) => void,
    children : React.ReactNode,
    onClick? : () => void
}

const INITIAL_SCALE = 1;

interface State {
    // The current "zoom state" of the map.
    transform : d3.ZoomTransform,
}

const initialState : State = {
    transform : d3.zoomIdentity.scale(INITIAL_SCALE)
}

export function snapToGrid(coords : Coords) : Coords {
    return { 
        x : Math.round(coords.x / 100) * 100, 
        y : Math.round(coords.y / 100) * 100 
    };
}


export default class AtlasGrid extends React.Component<Props, State> {
    svg         : RefObject<SVGSVGElement>
    gridPattern : RefObject<SVGPatternElement>
    container   : RefObject<SVGGElement>


    constructor(props : Props) {
        super(props);
        this.state = {...initialState};
        this.svg         = React.createRef();
        this.gridPattern = React.createRef();
        this.container   = React.createRef();
    }

    componentDidMount() { 
        console.assert(this.svg.current !== null);
        const svg = d3.select(this.svg.current);

        // Subscribe to the mouse position and store it in `this.state.cursor`
        svg.on("mousemove", () => {
            // Get zoom-invariant mouse position
            const pos = this.state.transform.invert(d3.mouse(this.svg.current as any));
            // Scale coordinates according to tile size
            const coords = { 
                x : pos[0],
                y : pos[1]
            };
            this.props.dispatch({ type : 'set mouse position', coords });
        });

        // Add zoom functionality to the SVG
        const zoom = d3.zoom()
            //.scaleExtent([0.1, 10])
            .clickDistance(5)
            .on("zoom", () => {
                this.setState({
                    transform: d3.event.transform.scale(INITIAL_SCALE)
                });
            });
        svg.call(zoom as any);
        // Don't permit "double-click to zoom"
        svg.on("dblclick.zoom", null);
    }

    componentDidUpdate() { 
        const svg         = d3.select(this.svg.current);
        const container   = d3.select(this.container.current);
        const gridPattern = d3.select(this.gridPattern.current);

        // Update the click listener or set it to a no-op
        const noop = () => {};
        svg.on("click", this.props.onClick || noop);

        // Apply the current transform state to both the grid and all child elements
        const transform = this.state.transform.toString();
        gridPattern.attr("patternTransform", transform);
        container.attr("transform", transform);
    }

    render() {
        return (
            <svg ref={this.svg} style={this.props.style}>
                <defs>
                    <pattern ref={this.gridPattern} id="atlas-grid-pattern" width="100" height="100" patternUnits="userSpaceOnUse">
                        <path d={`M 100 0 L 0 0 0 100`} fill="none" stroke="gray" strokeWidth="1" />
                    </pattern>
                </defs>

                <rect width="100%" height="100%" fill="url(#atlas-grid-pattern)" />

                <g ref={this.container}>
                    {this.props.children}
                </g>
            </svg>
        );
     }
}