
/** Returns a deep copy of the given JSON object. */
export function deepCopy(obj : Object) {
    return JSON.parse(JSON.stringify(obj));
}

/** Useful for making the Typescript compiler check for exhaustiveness
 * of switch statements.
 * See https://www.typescriptlang.org/docs/handbook/advanced-types.html#exhaustiveness-checking
 */
export function unreachable(_ : never): never {
    throw new Error("A `switch` statement failed to account for all possibilities");
}