import { Model, initialModel, createNode, createPathway, updatePathway, deletePathway, updateNode, deleteNode, createLink, updateLink, deleteLink } from "../model";
import { ModelMsg } from '../actions';
import { undoable } from "../utils/history";

function _model(
    model : Model | undefined = initialModel,
    msg : ModelMsg
) : Model {
    switch (msg.type) {
        case 'create node':
            return createNode(msg.node, model);

        case 'update node':
            return updateNode(msg.id, msg.node, model)

        case 'delete node':
            return deleteNode(msg.id, model)

        case 'create pathway':
            return createPathway(msg.pathway, model);

        case 'update pathway':
            return updatePathway(msg.id, msg.pathway, model);

        case 'delete pathway':
            return deletePathway(msg.id, model);

        case 'create link':
            return createLink(msg.link, model);

        case 'update link':
            return updateLink(msg.id, msg.link, model);

        case 'delete link':
            return deleteLink(msg.id, model);

        case 'set model':
            return msg.model;

        default:
            return model;
    }
}

export const model = undoable({ limit : 50 }, _model)