import React from 'react';
import { Coords, Link, Model, LinkId, PartialLink, Color } from '../../model';
import * as d3 from 'd3';
import { Dispatch } from 'redux';
import { ModelMsg } from '../../actions';
import { State as GlobalState } from '../App';
import { connect } from 'react-redux';
import DraggableLinkMarker from './DraggableLinkMarker';
import { getMouseCoords } from './utils';

export interface Props {
    id? : LinkId,
    link : Link | PartialLink,
    cursor? : Coords,
    model : Model,
    dispatch : Dispatch<ModelMsg>
}

/** 
 * The generator takes a list of coordinates and returns path data for 
 * a curve passing through them.
 */
const lineGenerator : d3.Line<Coords> = 
    d3.line<Coords>()
        .curve(d3.curveCardinal)
        .x(c => c.x)
        .y(c => c.y);

/**
 * Renders a link, given a set of points to pass through.
 */
const GenericLink = ({ points, color } : { points : Coords[], color : Color }) => (
    <path 
        className = { "has-stroke-" + color }
        d = { lineGenerator(points) || undefined }
        fill = "transparent"
        strokeWidth = "10"
    />
);

/**
 * Renders a specific link and its intermediate points.
 */
const AtlasLinkComponent = ({ id, link, model, cursor, dispatch } : Props) => {
    const { src, dst, intermediates } = link;
    const srcCoords = model.nodes[src].coords;
    const dstCoords = dst ? model.nodes[dst].coords : cursor!;
    // The list of intermediate points for the line, starting from the
    // source coordinates, passing through all intermediate coordinates,
    // and finally reaching the destination node coordinates.
    const points : Coords[] = ([] as Coords[]).concat([srcCoords], intermediates, [dstCoords]);

    const color = model.pathways[link.pathway].color;

    // Updates the coordinates of the i-th intermediate marker
    function updatePosition(coords : Coords, i : number) {
        if (id && link.dst) {
            const newIntermediates = intermediates.slice();
            newIntermediates[i] = coords;
            const newLink = { ...link, intermediates : newIntermediates };
            dispatch({ type : 'update link', link : newLink, id });
        }
    }

    return (<g>
        <GenericLink points={points} color={color} />
        { intermediates.map((coords, i) => 
            <DraggableLinkMarker 
                key={id + "-" + i}
                color={color}
                coords={coords} 
                updatePosition={c => updatePosition(c, i)}
            />
        ) }
    </g>);
};

export default connect(stateToProps, dispatchToProps)(AtlasLinkComponent);

function stateToProps(state : GlobalState) : Pick<Props,'model'> {
    return { model : state.model.present };
}

function dispatchToProps(dispatch : Dispatch<ModelMsg>) : Pick<Props,'dispatch'> {
    return { dispatch };
}