import React from 'react';
import { IconDefinition } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

export interface PanelItemProps {
    onClick : () => void,
    icon : IconDefinition,
    text : string
}

export interface Props {
    heading : string,
    panelItems : PanelItemProps[]
}


const PanelItem = ({ onClick, icon, text } : PanelItemProps) => (
    <label className="panel-block" 
        onClick={onClick}>
        <span className="panel-icon">
            <FontAwesomeIcon icon={icon} />
        </span>
        {text}
    </label>
)

export const Panel = ({ heading, panelItems } : Props) => (
    <nav className="panel">
        <div className="panel-heading">{heading}</div>

        { 
            panelItems.map(props => 
                <PanelItem key={props.text} {...props} />
            ) 
        }
    </nav>
)