import React, { RefObject } from 'react';
import { Coords, NodeId, Node } from '../../model';
import * as d3 from 'd3';
import { Dispatch } from 'redux';
import { ModelMsg } from '../../actions';
import { State as GlobalState } from '../App';
import { snapToGrid } from './AtlasGrid';
import { connect } from 'react-redux';
import { getMouseCoords } from './utils';

export interface Props {
    id : NodeId
    node : Node,
    onClick? : () => void,
    dispatch : Dispatch<ModelMsg>
}

interface State {
    // If the node is being dragged, this field will hold the mouse coordinates
    dragPosition : undefined | Coords
}

/**
 * Create an action which updates the given node's coordinates.
 */
function setPosition(id : NodeId, oldNode : Node, coords : Coords) : ModelMsg {
    // Note that d3 automatically adjusts these x,y coordinates to the zoom
    // of the svg element
    const node = { ...oldNode, coords };

    return { 'type' : 'update node', id, node };
}

/**
 * A draggable node to be rendered on the atlas.
 * Dragging causes the node to dispatch actions that change the node's position.
 */
class AtlasNodeComponent extends React.Component<Props,State> {

    ref : RefObject<SVGCircleElement>

    constructor(props : Props) {
        super(props);
        this.ref = React.createRef();
        this.state = { dragPosition : undefined };
    }

    componentDidMount() {
        console.assert(this.ref.current !== null);

        // Set the drag handler for this node
        const el = d3.select(this.ref.current);
        const drag = d3.drag()
            .clickDistance(5)
            .on('drag', () => {
                this.setState({ dragPosition : snapToGrid(getMouseCoords()) });
            })
            .on('end', () => {
                const { dispatch, id, node } = this.props;
                const { dragPosition } = this.state;
                if (dragPosition !== undefined) {
                    dispatch(setPosition(id, node, dragPosition));
                    this.setState({ dragPosition : undefined });
                } 
            });
        el.call(drag as any);

        el.on('click', () => {
            if (this.props.onClick) this.props.onClick();
            d3.event.stopPropagation();
        });
    }

    render() {
        const coords = this.state.dragPosition || this.props.node.coords;
        const dragging = this.state.dragPosition !== undefined;

        return <circle 
            className = { "node" }
            opacity = { dragging ? 0.5 : 1 }
            ref = { this.ref }
            cx = { coords.x } 
            cy = { coords.y }
            r = { 20 }
        />;
    }
}



export default connect(stateToProps, dispatchToProps)(AtlasNodeComponent);

function stateToProps(state : GlobalState) : {} {
    return { };
}

function dispatchToProps(dispatch : Dispatch<ModelMsg>) : Pick<Props, 'dispatch'>{
    return { dispatch };
}