import React from 'react';
import { faCircle } from '@fortawesome/free-solid-svg-icons'
import {Color,Colors,Handler} from '../../model';
import Icon from '../generic/Icon';

interface Props {
    selected? : Color,
    onClick : Handler<Color>
}


export default function SelectColor(props : Props) {
    function style(color : Color) {
        return {
            borderColor: color === props.selected ? '#777' : 'transparent',
            borderWidth: '2px'
        }
    }

    return (<div className="buttons is-centered">
        {Colors.map((color) => 
            <button onClick={(e) => props.onClick(color)}
              className="button is-hovered is-large" key={color} type="button" style={style(color)}>
                <Icon icon={faCircle} color={color} container='medium' iconSize='2x' />
            </button>)}
    </div>)
}