import React from 'react';
import Icon from '../generic/Icon';
import { faCircle, faPlus } from '@fortawesome/free-solid-svg-icons'
import {Handler, Pathway, PathwayId, Pathways, getIds} from '../../model';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import ToolbarHeader from './ToolbarHeader';

interface Props {
    pathways : Pathways, 
    onCreate? : Handler<any>,
    onSelect : Handler<PathwayId>,
    goBack : Handler<any>,
}

export default function SelectOrCreatePathway(props : Props) {
    return (
    <ToolbarHeader title="Pathways" goBack={props.goBack}>

        {getIds(props.pathways).map((id) => 
            <PathwayItem key={id} {...props.pathways[id]} selectPathway={_ => props.onSelect(id)} />
        )}

        {props.onCreate &&
            <div className="panel-block">
                <button onClick={props.onCreate} type="button" className="button is-white is-fullwidth">
                    <Icon icon={faPlus} />
                    <span> Create a new pathway</span>
                </button>
            </div>
        }

    </ToolbarHeader>);
}

function PathwayItem(props : Pathway & { selectPathway : Handler<any> }) {
    return (<label className="panel-block" onClick={props.selectPathway}>
                <span className={"panel-icon has-text-" + props.color}>
                    <FontAwesomeIcon icon={faCircle} />
                </span>
                {props.name}
            </label>);
}