import React from 'react';
import { Coords, Model, nodes, NodeId, links, PathwayId, PartialLink, Link } from '../../model';
import { Msg } from '../../actions';
import { State as GlobalState } from '../App';
import { Dispatch } from 'redux';
import AtlasGrid, { snapToGrid } from './AtlasGrid';
import AtlasNode from './AtlasNode';
import GhostNode from './GhostNode';
import { connect } from 'react-redux';
import { unreachable } from '../../utils/utils';
import AtlasLink from './AtlasLink';
import { getMouseCoords } from './utils';


interface Props {
    state : State,
    style : React.CSSProperties,
    model : Model,
    dispatch : (msg : Msg) => void
}

export type Mode 
    = { type : 'Normal' }
    | { type : 'CreateNode' }
    | { type : 'SelectLinkSource', pathway : PathwayId }
    | { type : 'SelectLinkTarget', link : PartialLink }

export interface State {
    mode : Mode,
    cursor? : Coords
}

function Atlas({ style, state, dispatch, model } : Props) {
    const { cursor, mode } = state;

    const pickNodeLocation = () => {
        if (cursor)
            dispatch({ 
                type : 'prompt to create node with coords', 
                coords : snapToGrid(cursor)
            });
    };
    const editNode = (id : NodeId) => {
        dispatch({ type: 'prompt to edit node', id });
    };

    const onClickGrid = () => {
        switch (mode.type) {
            case 'CreateNode':
                return pickNodeLocation();

            case 'SelectLinkTarget':
                // Add this location to the list of intermediate points
                const intermediates = ([] as Array<Coords>).concat(
                    mode.link.intermediates, 
                    [snapToGrid(state.cursor!)]
                );
                const link = { ...mode.link, intermediates };
                return dispatch({ type : 'prompt to select link target', link })

            case 'SelectLinkSource':
            case 'Normal':
                return;
        }
        return unreachable(mode);
    };

    const onClickNode = (id : NodeId) => () => {
        switch (mode.type) {
            case 'Normal':
                return editNode(id);
            case 'CreateNode':
                return;
            case 'SelectLinkSource':
                return dispatch({ 
                    type : 'prompt to select link target', 
                    link : { 
                        src : id, 
                        dst : undefined, 
                        pathway : mode.pathway, 
                        intermediates : [] 
                    }
                });
            case 'SelectLinkTarget':
                dispatch({ type : 'create link', link : { ...mode.link, dst : id } });
                dispatch({ type : 'prompt to select link source', pathway : mode.link.pathway });
                return;
        }
        return unreachable(mode);
    };

    const shouldRenderGhostNode = () => {
        switch (mode.type) {
            case 'CreateNode':
                return true;
        }
        return false;
    };

    return (
        <AtlasGrid style={style} dispatch={dispatch} onClick={ onClickGrid }>
            {
                links(model).map(([id, link]) =>
                    <AtlasLink key={id} id={id} link={link} />
                )
            }
            {
                mode.type === 'SelectLinkTarget' &&
                    <AtlasLink link={mode.link} cursor={cursor && snapToGrid(cursor)} />
            }
            { 
                nodes(model).map(([id, node]) => 
                    <AtlasNode key={id} id={id} node={node} onClick={ onClickNode(id) } />
                )
            }
            {
                cursor && shouldRenderGhostNode() &&
                <GhostNode coords={ snapToGrid(cursor) } />
            }
        </AtlasGrid>
    );
}

export default connect(stateToProps, dispatchToProps)(Atlas);

function stateToProps(state : GlobalState) : Pick<Props, 'state' | 'model'> {
    return { 
        state : state.ui.atlas, 
        model : state.model.present 
    };
}

function dispatchToProps(dispatch : Dispatch<Msg>) : Pick<Props, 'dispatch'>{
    return { dispatch };
}