/**
 * This file defines the types for our data model and the methods for modifying
 * it. Since we are using Redux, it must be kept immutable and all the methods
 * return new objects.
 */

 /**** TYPES ****/

export const initialModel : Model = {
    pathways : {},
    nodes    : {},
    links    : {},
}

export interface Model {
    readonly pathways : Pathways,
    readonly nodes    : Nodes,
    readonly links    : Links
}

export type Pathways = Indexable<Pathway>
export type Nodes = Indexable<Node>
export type Links = Indexable<Link>

export interface Indexable<T> {
    readonly [id : number] : T
}

export interface Node {
    readonly name : string,
    readonly coords : Coords
}

export interface Link {
    readonly src : NodeId,
    readonly dst : NodeId,
    readonly intermediates : Coords[],
    readonly pathway : PathwayId
}

export interface Pathway {
    readonly name : string,
    readonly color : Color
}

/** A partially intitialized link. Useful for when the user is initializing
 * the link over time.
 */
export interface PartialLink {
    readonly src : NodeId,
    readonly dst : undefined,
    readonly intermediates : Coords[],
    readonly pathway : PathwayId
}

/** This type should hold coordinates in "absolute position", i.e. according to
 * their distance in tile units from 0, 0. By storing data in this generic format,
 * we can change the tile size without changing the semantics of existing data.
 */
export interface Coords {
    readonly x : number,
    readonly y : number
};

export type Color                  =  'light-blue' | 'purple' | 'light-green' | 'red' | 'orange-yellow';
export const Colors : Array<Color> = ['light-blue' , 'purple' , 'light-green' , 'red' , 'orange-yellow'];

export type NodeId = number;
export type LinkId = number;
export type PathwayId = number;

export type Handler<A> = (a : A) => void;
export type Handler2<A,B> = (a : A, b : B) => void;


/**** ACCESSOR FUNCTIONS ****/


export function getIds<T>(obj : Indexable<T>) : Array<number> {
    return Object.keys(obj).map((k : string) => parseInt(k));
}

function newId<T>(obj : Indexable<T>) : number {
    const ids = getIds(obj);
    if (ids.length === 0) {
        return 1;
    }
    else {
        return 1 + Math.max(...ids);
    }
}


/**
 * Return a list of all nodes in the model, each with an `id` field
 * containing their id.
 */
export function nodes(model : Model) : [NodeId,Node][] {
    return getIds(model.nodes).map((id) => {
        return [id, model.nodes[id]];
    });
}

/**
 * Return a list of all links in the model, each with an `id` field
 * containing their id.
 */
export function links(model : Model) : [LinkId, Link][] {
    return getIds(model.links).map((id) => {
        return [id, model.links[id]];
    });
}

/**
 * Return a list of all pathways in the model, each with an `id` field
 * containing their id.
 */
export function pathways(model : Model) : [PathwayId, Pathway][] {
    return getIds(model.pathways).map((id) => {
        return [id, model.pathways[id]];
    });
}


/**** UPDATE FUNCTIONS ****/


export function createPathway(pathway : Pathway, model : Model) : Model {
    const id = newId(model.pathways);
    let pathways = { ...model.pathways };
    pathways[id] = pathway;

    return { ...model, pathways };
}

export function updatePathway(id : PathwayId, pathway : Pathway, model : Model) : Model {
    let pathways = { ...model.pathways };
    pathways[id] = pathway;

    return { ...model, pathways };
}

export function deletePathway(id : PathwayId, model : Model) : Model {
    let pathways = { ...model.pathways };
    delete pathways[id];

    // Filter out all links belonging to this pathway
    let links : { [id : number] : Link } = {};
    for (let linkID in model.links) {
        const link = model.links[linkID];
        if (link.pathway !== id) {
            links[linkID] = link;
        }
    }

    return { ...model, links, pathways };
}

export function createNode(node : Node, model : Model) : Model {
    const id = newId(model.nodes);
    let nodes = { ...model.nodes };
    nodes[id] = node;

    return { ...model, nodes };
}

export function updateNode(id : NodeId, node : Node, model : Model) : Model {
    let nodes = { ...model.nodes };
    nodes[id] = node;

    return { ...model, nodes };
}

export function deleteNode(id : NodeId, model : Model) : Model {
    let nodes = { ...model.nodes };
    delete nodes[id];

    // Remove all links involving this node
    let links : { [id : number] : Link } = {};
    for (let linkID in model.links) {
        const link = model.links[linkID];
        if (link.src !== id && link.src !== id) {
            links[linkID] = link;
        }
    }

    return { ...model, nodes, links };
}

export function createLink(link : Link, model : Model) : Model {
    const id = newId(model.links);
    let links = { ...model.links };
    links[id] = link;

    return { ...model, links };
}

export function updateLink(id : LinkId, link : Link, model : Model) : Model {
    let links = { ...model.links };
    links[id] = link;

    return { ...model, links };
}

export function deleteLink(id : LinkId, model : Model) : Model {
    let links = { ...model.links };
    delete links[id];

    return { ...model, links };
}